import { useDispatch, useSelector } from 'react-redux';
import { useEffect, Suspense, lazy } from 'react';
import { Switch, Route } from 'react-router-dom';
import Container from './components/Container/container';
import PrivateRoute from './components/PrivateRoute';
import PublicRoute from './components/PublicRoute';
import AppBar from './components/AppBar';
import Phonebook from './components/PhoneBook/phoneBook';
import Contacts from './components/Contacts/contacts';
import Filter from './components/Filter/filter';
import { getContacts } from './redux/actions';
/* import  operations  from '../src/redux/auth/auth-operations'; */
//++++++++++++++++++++++++++++++++++++++++++++++++++++++++

/* import TodosView from './views/TodosView';*/
import HomeView from './views/HomeView';
import RegisterView from './views/RegisterView';
import LoginView from './views/LoginView';
import  {authSelectors}  from '../src/redux/auth/auth-selectors';
import   {authOperations}  from '../src/redux/auth/auth-operations';
import './App.css';
//const Contacts = lazy(() => import('./components/Contacts/contacts'));
//++++++++++++++++++++++++++++++++++++++++++++++++++++++++++




export default function App() {
  const dispatch = useDispatch();
  const isFetchingCurrentUser = useSelector(authSelectors.getIsFetchingCurrent);

  useEffect(() => {
    dispatch(authOperations.fetchCurrentUser());
  }, [dispatch]);
 
  useEffect(() => {
    dispatch(getContacts());
  }, [dispatch]);
  
  return (
    
      <Container>
        {isFetchingCurrentUser ? (
        <h1>–ü–æ–∫–∞–∑—ã–≤–∞–µ–º React Skeleton</h1>
      ) : (

        <>
        <AppBar />
        <Switch>
            <Suspense fallback={<p>–ó–∞–≥—Ä—É–∂–∞–µ–º...</p>}>
              <PublicRoute exact path="/">
                <HomeView />
              </PublicRoute>
              <PublicRoute exact path="/register" restricted>
                <RegisterView />
              </PublicRoute>
              <PublicRoute exact path="/login" redirectTo="/contacts" restricted>
                <LoginView />
              </PublicRoute>
              <PrivateRoute path="/contacts" redirectTo="/login">
              <Phonebook />
              <Filter />
              <Contacts />
              </PrivateRoute>
              
            </Suspense>
          </Switch>


        {/* <Route exact path="/" component={HomeView} /> */}

        {/* <Route path="/register" component={RegisterView} />
        <Route path="/login" component={LoginView} /> */}
        {/* <Route path="/todos" component={TodosView} /> */}
       
        </>)}

      </Container>
    
  );
}


